@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400&display=swap");
.footer {
  background-color: #2E1F17;
  color: #f1f1f1;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 4em;
  height: 70vh;
}
.footer a {
  text-decoration: none;
  color: #c1c1c1;
  display: flex;
  text-transform: uppercase;
  font-family: "Josefin Sans";
  transition: 0.4s ease-in-out;
  letter-spacing: 0.7px;
  font-size: 0.9rem;
}
.footer a:hover {
  color: #f1f1f1;
  transition: 0.4s ease-in-out;
}
.footer .left-side-footer {
  margin-top: 2em;
}
.footer .center-footer {
  display: flex;
  gap: 6em;
}
.footer .center-footer h4 {
  text-transform: uppercase;
  font-family: "Josefin Sans";
}
.footer .center-footer ul {
  list-style-type: none;
  padding: 0;
}
.footer .center-footer ul li {
  font-family: "Josefin Sans";
  font-weight: 300;
  margin-top: 0.8em;
}
.footer .center-footer ul li a {
  font-size: 0.9rem;
  text-transform: unset;
}
.footer .right-col-footer {
  justify-content: center;
  align-items: center;
}
.footer .right-col-footer a {
  margin-bottom: 2em;
  font-weight: 300;
  text-align: right;
}

@media screen and (min-width: 680px) {
  .footer {
    height: 50vh;
  }
}
@media screen and (min-width: 800px) {
  .footer {
    display: flex;
    flex-direction: unset;
    justify-content: space-between;
    height: 20vh;
    align-items: center;
  }
  .left-side-footer {
    margin-top: 1em;
    margin-left: 2em !important;
    justify-content: center;
    align-items: center;
  }
  .left-side-footer svg {
    width: 80%;
    height: auto;
  }
  .center-footer {
    display: flex;
    gap: 6em;
  }
  .center-footer h4 {
    text-transform: uppercase;
    font-family: "Josefin Sans";
  }
  .center-footer ul {
    list-style-type: none;
    padding: 0;
  }
  .center-footer ul li {
    font-family: "Josefin Sans";
    font-weight: 300;
    margin-top: 0.8em;
  }
  .center-footer ul li a {
    font-size: 0.9rem;
    text-transform: unset;
  }
  .right-col-footer {
    margin-right: 3em !important;
    margin-top: 2em;
  }
}
@media screen and (min-width: 1000px) {
  .footer {
    height: 30vh;
  }
}